body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: linear-gradient(
    to right,
    rgba(7, 27, 82, 1) 0%,
    rgba(0, 128, 128, 1) 100%
  );
}

.card-list {
  justify-content: center;
}

.image-list .card {
  margin: 10px;
}

.search-input {
  margin-left: auto;
  margin-right: auto;
}

.header-text {
  font-family: "Indie Flower", sans-serif;
  font-weight: 900;
  font-size: 6em;
  color: #0ccac4;
}

.container {
  margin: 20px;
  text-align: center;
}
